import React, { Component } from 'react';
import { Link } from "react-router-dom";

class LegalTabs extends Component {

    render() {

        this.currentLink = window.location.pathname

        return (
            <ul className="legalLinks">
                <li className={`legalLink ${this.currentLink === '/aviso-legal' ? 'active' : ''}`}>
                    <Link to="/aviso-legal" >
                        Aviso Legal
                    </Link>
                </li>

                <li className={`legalLink ${this.currentLink === '/politica-privacidad' ? 'active' : ''}`}>
                    <Link to="/politica-privacidad">
                        Política de Privacidad
                    </Link>
                </li>

                <li className={`legalLink ${this.currentLink === '/politica-cookies' ? 'active' : ''}`}>
                    <Link to="/politica-cookies">
                        Política de Cookies
                    </Link>
                </li>
            </ul>
        )
    }
}

export default LegalTabs