import React from 'react';
import CircularProgress from '@material-ui/core/CircularProgress';

function Progress() {

    return (
        <div className="cont-all-screen cont-all-center">
            <CircularProgress
                variant="indeterminate"
                size={40}
                thickness={2}
                color="primary" />
        </div>
    );       
}

export default Progress;