import React, { useState, useEffect } from 'react';
import { useTranslation } from 'react-i18next';

import cartelJpg from '../img/us/cartel.jpg';
import cartelWebp from '../img/us/cartel.webp';
import historiaJpg from '../img/us/historia.jpg';
import historiaWebp from '../img/us/historia.webp';
import josebaJpg from '../img/us/joseba.jpg';
import josebaWebp from '../img/us/joseba.webp';
import aitaJpg from '../img/us/aita.jpg';
import aitaWebp from '../img/us/aita.webp';
import equipoJpg from '../img/us/equipo.jpg';
import equipoWebp from '../img/us/equipo.webp';

import Grid from '@material-ui/core/Grid';
import Box from '@material-ui/core/Box';

import CircularProgress from '@material-ui/core/CircularProgress';


function Us() {
    const { t, i18n } = useTranslation();
    const lang = i18n.language;
    const [load, setLoad] = useState(false);

    const printEspecialEusk = () => {
        if (lang === 'eus') {

            return (
                <>
                    <p className="left-text">
                        <span className="text3 small">{t('us.text1A')}</span>
                        <span className="text2">{t('us.text1B')}</span> 
                    </p>

                    <p><span className="text1">{t('us.text1C')}</span></p>
          
                    <p className="right-text">
                        <span className="text2">{t('us.text1D')}</span>
                        <span className="text3 small">{t('us.text1E')}</span></p>
                </>
            )
        } else {
            return (
                <>
                <p className="left-text">
                    <span className="text2">{t('us.text1A')}</span> <span className="text3 small">{t('us.text1B')}</span>
                    <span className="text2">{t('us.text1C')}</span>
                </p>
                <p><span className="text1">{t('us.text1D')}</span></p>
                <p>
                    <span className="text2">{t('us.text1E')}</span>
                    <span className="text3">{t('us.text1F')}</span>
                </p>
                <p className="right-text"><span className="text1">{t('us.text1G')}</span></p>
                </>
            )
        }
    }

    const printEspecialEusk2 = () => {
        if (lang === 'eus') {
            return (
                <p className="right-text"><span className="text1" id="specialtext1">{t('us.text2A')}</span>{t('us.text2B')}</p>
            )
        } else {
            return (
                <p className="right-text">{t('us.text2A')}<span className="text1" id="specialtext1">{t('us.text2B')}</span></p>
            )
        }
    }

    const printEspecialEusk3 = () => {
        if (lang === 'es') {
            return (
                <>
                    <p><span className="text1 bgContrast" id="specialText2">{t('us.text2D')}</span></p>
                    <p>{t('us.text2E')}</p>
                </>
            )
        } else {
            return (
                <>
                    <p>{t('us.text2D')}</p>
                    <p><span className="text1 bgContrast" id="specialText2">{t('us.text2E')}</span></p>
                    <p>{t('us.text2F')}</p>
                </>
            )
        }
    }


    useEffect(() => {
        setLoad(true);
    }, []);


    if (load === false) {
        return (
            <div className="cont-all-screen cont-all-center">
                <CircularProgress
                    variant="indeterminate"
                    size={40}
                    thickness={2}
                    color="primary" />
            </div>
        );

    } else {
        return (
            <>

                <picture className="complete-width">
                    <source srcSet ={cartelWebp} type="image/webp" />
                    <source srcSet={cartelJpg} type="image/jpg" />
                    <img src={cartelJpg} alt="Imagen de un cartel emblemático del bar" />
                </picture>

                <div className="spacer"></div>

                <div className="text-div">
                    <Grid
                        container
                        direction="column"
                        justify="center"
                        alignItems="center"
                    >
                        <Grid item>
                            <Box m={2} >
                                {printEspecialEusk()}
                            </Box>
                        </Grid>

                        <Grid item className="us-arrows-cont">
                            <Box mb={8} className="cont-all-center">
                                <div className="us-arrow">
                                    <span></span>
                                    <span></span>
                                    <span></span>
                                </div>
                                <div className="us-arrow">
                                    <span></span>
                                    <span></span>
                                    <span></span>
                                </div>
                            </Box>
                        </Grid>

                    </Grid>
                </div>
                

                <div className="spacer"></div>

                <div className="us-bg-div text-div">

                    <div className="tradition">
                        <Grid
                            container
                            direction="row"
                            justify="center"
                            alignItems="center"
                            spacing={2}
                            wrap="wrap"
                        >
                            <Grid item sm={12} md={6} lg={4} >
                                    <picture>
                                        <source srcSet={historiaWebp} type="image/webp" />
                                        <source srcSet={historiaJpg} type="image/jpg" />
                                        <img src={historiaJpg} alt="Imagen de los inicios del Ezkurra" />
                                    </picture>
                            </Grid>

                            <Grid item sm={12} md={6} lg={4}>
                                <Box mb={4} mt={2}>
                                    {printEspecialEusk2()}
                                </Box>
                                    <p className="justify-text">{t('us.text2C')}</p>
                            </Grid>
                        </Grid>
                    </div>

                    <div className="now">
                        <Grid
                            container
                            direction="row"
                            justify="center"
                            alignItems="center"
                            spacing={6}
                            wrap="wrap"
                            className="color-div"
                        >
                            <Grid item sm={12} md={6} lg={4} >
                                <picture>
                                    <source srcSet={aitaWebp} type="image/webp" />
                                    <source srcSet={aitaJpg} type="image/jpg" />
                                    <img src={aitaJpg} alt="Imagen del aita de Joseba" />
                                </picture>
                            </Grid>

                            <Grid item sm={12} md={6} lg={4} >
                                <picture>
                                    <source srcSet={josebaWebp} type="image/webp" />
                                    <source srcSet={josebaJpg} type="image/jpg" />
                                    <img src={josebaJpg} alt="Imagen de Joseba" />
                                </picture>
                            </Grid>
                        </Grid>

                        <Box mt={4}>
                            {printEspecialEusk3()}
                            
                        </Box>
                    </div>
                </div>
                
                <div className="spacer"></div>

                <div className="text-div" id="jefas">

                    <Box className="cont-all-center">
                        <picture>
                            <source srcSet={equipoWebp} type="image/webp" />
                            <source srcSet={josebaJpg} type="image/jpg" />
                            <img src={equipoJpg} alt="Joseba y las dos jefas de cocina" />
                        </picture>
                    </Box>

                    <Box className="img-bottom">   
                        <p>
                            <span className="text4 bgContrast">{t('us.text3A')}</span>
                            <span className="text2 big">{t('us.text3B')}</span>
                            <span className="text1">{t('us.text3C')}</span>
                            <span className="text2 small">{t('us.text3D')}</span>
                            <span className="text3">{t('us.text3E')}</span>
                        </p>
                     </Box>
                </div>
                <div className="spacer"></div>
                
            </>
        );
    }
}

export default Us;
