import React from 'react';
import { BrowserRouter as Router, Route } from 'react-router-dom';

import './css/styles.css';

import Header from './template/Header';
import Footer from './template/Footer';

import Home from './app/Home';
import Us from './app/Us';
import Menu from './app/Carta';
import TakeAway from './app/TakeAwayProv/TakeAway';

import AvisoLegal from './app/legal/AvisoLegal';
import CookiesP from './app/legal/Cookies';
import Privacidad from './app/legal/Privacidad';

import Cookies from './components/Cookies';
import ScrollToTopBtn from './components/ScrollToTopBtn';
import ScrollToTop from './components/ScrollToTop';

function App() {
  
    return (
        <Router >
            <ScrollToTop />
            <div className="App">

                <Cookies />

                <Header />

                <div className="container">

                    <Route exact path="/" component={Home} />
                    <Route exact path="/nosotros" component={Us} />
                    <Route exact path="/carta" component={Menu} />
                    <Route exact path="/para-llevar" component={TakeAway} />

                    <Route exact path="/aviso-legal" component={AvisoLegal} />
                    <Route exact path="/politica-cookies" component={CookiesP} />
                    <Route exact path="/politica-privacidad" component={Privacidad} />

                    <ScrollToTopBtn />

                </div>

                <Footer />

            </div>
        </Router>
  );
}

export default App;
