import React, { useState, useEffect, useRef, useCallback  }from 'react';
import { useTranslation } from 'react-i18next';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';

import pintxo from '../img/carta/pintxo.png';
import plato from '../img/carta/plato.png';
import racion from '../img/carta/racion.png';
import postre from '../img/carta/postre.png';
import ensalada from '../img/carta/ensalada.png';
import hamburguesa from '../img/carta/hamburguesa.png';
import sandwich from '../img/carta/sandwich.png'; 
import bocata from '../img/carta/bocata.png'; 
import pintxow from '../img/carta/pintxo.webp';
import platow from '../img/carta/plato.webp';
import racionw from '../img/carta/racion.webp';
import postrew from '../img/carta/postre.webp';
import ensaladaw from '../img/carta/ensalada.webp';
import hamburguesaw from '../img/carta/hamburguesa.webp';
import sandwichw from '../img/carta/sandwich.webp';
import bocataw from '../img/carta/bocata.webp'; 

import FetchData from '../utils/FetchData';
import { getProductName, getProductDescription } from '../utils/GetDataLang';
import Progress from '../components/Progress';

const itemsDiv = (lang, specific) => {
    return (

    <div className="menuGroupDiv">
            {specific.map(data => (

                <div className="menuItemDiv" key={data.id}>

                    {data.descEs != null &&
                        <>
                        <p className="name">{getProductName(data, lang)}</p>
                            <p className="prop">
                            <span className="desc">{getProductDescription(data, lang)}</span>
                                <span className="price">{(data.price).toFixed(2)}</span>
                            </p>
                        </>
                    }

                    {data.descEs == null &&
                        <p className="prop">
                        <span className="name">{getProductName(data, lang)}</span>
                            <span className="price">{(data.price).toFixed(2)}</span>
                        </p>
                    }

                </div>
            ))}
    </div>

    )
}

const itemsTitle = (title) => {
    return (
        <div className="title-double-line">
            <div><div></div></div>
            <h4>{title}</h4>
            <div><div></div></div>
        </div>
    )
}

const itemsImage = (image, imagew) => {
    return (
        <div className="img">
            <picture>
                <source srcSet={imagew} type="image/webp" />
                <source srcSet={image} type="image/png" />
                <img src={image} alt="Imagen de comida" />
            </picture>
        </div>
    )
}

const itemsCard = (lang, specific, title, image, imagew, reference) => {
    return (
        <div ref={reference} className="itemsCard">
            { itemsTitle(title)}
            { itemsDiv(lang, specific)}
            { itemsImage(image, imagew)}
        </div >
    );
}


function Menu() {

    const { t, i18n } = useTranslation();
    const lang = i18n.language;

    const [value, setValue] = useState(0);
    const [menu, setMenu] = useState();
    const [load, setLoad] = useState(false);

    const input0 = useRef();
    const input1 = useRef();
    const input2 = useRef();
    const input3 = useRef();
    const input4 = useRef();
    const input5 = useRef();
    const input6 = useRef();
    const input7 = useRef();
   

    const handleChange = (event, newValue) => {
        setValue(newValue);
    };

    const scrollToGroup = (reference) => {
        var stickyHeaderHeight = 130;

        if (reference.current) {
            reference.current.scrollIntoView(true);

            var scrolledY = window.scrollY;
            if (scrolledY) {
                window.scroll(0, scrolledY - stickyHeaderHeight);
            }

        }
    }

    const scrollHandler = useCallback(() => {

        if (window.pageYOffset + window.innerHeight >= input7.current.offsetTop) {
            setValue(7);
        } else if (window.pageYOffset + window.innerHeight >= input6.current.offsetTop) {
            setValue(6);
        } else if (window.pageYOffset + window.innerHeight >= input5.current.offsetTop) {
            setValue(5);
        } else if (window.pageYOffset + window.innerHeight >= input4.current.offsetTop) {
            setValue(4);
        } else if (window.pageYOffset + window.innerHeight >= input3.current.offsetTop) {
            setValue(3);
        } else if (window.pageYOffset + window.innerHeight >= input2.current.offsetTop) {
            setValue(2);
        } else if (window.pageYOffset + window.innerHeight >= input1.current.offsetTop) {
            setValue(1);
        } else if (window.pageYOffset + window.innerHeight >= input0.current.offsetTop) {
            setValue(0);
        }

    }, []);

    useEffect(() => {
        FetchData(setMenu, setLoad, 'carta');
    }, []);


    useEffect(() => {
        if (load === true) {
            window.addEventListener('scroll', scrollHandler);
        }
        return () => window.removeEventListener('scroll', scrollHandler);
    }, [scrollHandler, load]);

    if (load === false) {
        return (
            <Progress/>
        );

    } else {

        return (

            <div id="darkBg">

                <AppBar position="sticky" className="cartaBar">
                    <Tabs
                        value={value}
                        onChange={handleChange}
                        variant="scrollable"
                        scrollButtons="on"
                    >
                        <Tab label={t('menu.pintxo')} onClick={() => scrollToGroup(input0)}/>
                        <Tab label={t('menu.rac')} onClick={() => scrollToGroup(input1)} />
                        <Tab label={t('menu.boc')} onClick={() => scrollToGroup(input2)} />
                        <Tab label={t('menu.hamb')} onClick={() => scrollToGroup(input3)} />
                        <Tab label={t('menu.sand')} onClick={() => scrollToGroup(input4)} />
                        <Tab label={t('menu.salad')} onClick={() => scrollToGroup(input5)} />
                        <Tab label={t('menu.pcomb')} onClick={() => scrollToGroup(input6)} />
                        <Tab label={t('menu.desert')} onClick={() => scrollToGroup(input7)} />
                    </Tabs>
                </AppBar>

                <div className="max-width-cont">

                    {menu && itemsCard(lang, menu.pintxo, t('menu.pintxo'), pintxo, pintxow, input0)}
                    {menu && itemsCard(lang, menu.racion, t('menu.rac'), racion, racionw, input1)}
                    {menu && itemsCard(lang, menu.bocata, t('menu.boc'), bocata, bocataw, input2)}
                    {menu && itemsCard(lang, menu.hamburguesa, t('menu.hamb'), hamburguesa, hamburguesaw, input3)}
                    {menu && itemsCard(lang, menu.sandwich, t('menu.sand'), sandwich, sandwichw, input4)}
                    {menu && itemsCard(lang, menu.ensalada, t('menu.salad'), ensalada, ensaladaw, input5)}

                   
                    <div ref={input6} className="itemsCard">
                        {itemsTitle(t('menu.pcomb'))}

                        <h2 className="note">{t('menu.aGusto')}</h2>

                        <div className="menuGroupDiv">
                            <h2>{t('menu.pppal')}</h2>
                            {menu && menu.pc_ppal.map(data => (
                                <div className="menuItemDiv" key={data.id}>
                                    <p className="prop">
                                        <span className="name">{getProductName(data, lang)}</span>
                                        <span className="price">{(data.price).toFixed(2)}</span>
                                    </p>
                                </div>
                            ))}

                            <h2>{t('menu.psec')}</h2>
                            {menu && menu.pc_acom.map(data => (
                                <div className="menuItemDiv" key={data.id}>
                                    <p className="prop">
                                        <span className="name">{getProductName(data, lang)}</span>
                                        <span className="price">{(data.price).toFixed(2)}</span>
                                    </p>
                                </div>
                            ))}
                        </div>

                        {itemsImage(plato, platow)}
                       
                    </div>

                    {menu && itemsCard(lang, menu.postre, t('menu.desert'), postre, postrew, input7)}
                </div>

            </div>
        );
    }

}

export default Menu;
