const getProductName = (data, lang) => {
    switch (lang) {
        case 'es':
            return data.nombre;
        case 'eus':
            return data.izena;
        case 'cat':
            return data.nom;
        default:
            return null;
    }
}

const getProductDescription = (data, lang) => {
    switch (lang) {
        case 'es':
            return data.descEs;
        case 'eus':
            return data.descEus;
        case 'cat':
            return data.descCat;
        default:
            return null;
    }
}

const renameGroup = (type, t) => {
    switch (type) {
        case 1:
            return (t('take-away.racion'));
        case 2:
            return (t('take-away.rusa'))
        case 3:
            return (t('take-away.combo'))
        case 4:
            return (t('take-away.hamburguesa'))
        case 5:
            return (t('take-away.bocata'))
        case 6:
            return (t('take-away.sandwich'));
        case 7:
            return (t('take-away.postre'));
        default:
            return ('');
    }
}

export {
    getProductName,
    getProductDescription,
    renameGroup,
}