import React from 'react';
import { Link } from "react-router-dom";
import CookieConsent from "react-cookie-consent";

function Cookies() {
    return (
        <CookieConsent
            buttonText="ACEPTAR"
            extraCookieOptions={{ domain: "barezkurra.com" }}
            cookieName="CookieConsent"
            expires= {365}
        >

            <div className="cont-all-center">
                <p>Utilizamos cookies para darte una mejor experiencia.</p>
                <p><Link to="/politica-cookies">+ info</Link></p>
            </div>

        </CookieConsent>
    );
}

export default Cookies;
