import axios from 'axios';
const API_PATH = 'https://www.barezkurra.com/API/web_api/index.php';

const FetchData = async (setMenu, setLoad, request) => {
    await axios({
        method: 'post',
        url: `${API_PATH}`,
        headers: {
            'content-type': 'application/json'
        },
        data: { "request": request }
    })

        .then(result => {
            setMenu(result.data.items)
            setLoad(true)
        })
        .catch(error => console.log(error))
};

export default FetchData;