import React from 'react';
import { Link } from "react-router-dom";
import { useTranslation } from 'react-i18next';


function PpalMenu() {

    const currentLink = window.location.pathname;
    const { t } = useTranslation();


    return (
        <>
            <ul className="navbar-nav">
                <li className={`nav-item ${currentLink === '/' ? 'active' : ''}`}>
                    <Link to="/" className="nav-link"> {t('web-page.home')} </Link>
                </li>
                <li className={`nav-item ${currentLink === '/nosotros' ? 'active' : ''}`}>
                    <Link to="/nosotros" className="nav-link"> {t('web-page.us')} </Link>
                </li>
                <li className={`nav-item ${currentLink === '/carta' ? 'active' : ''}`}>
                    <Link to="/carta" className="nav-link"> {t('web-page.menu')} </Link>
                </li>
                <li className={`nav-item ${currentLink === '/para-llevar' ? 'active' : ''}`}>
                    <Link to="/para-llevar" className="nav-link"> {t('web-page.take-away')} </Link>
                </li>
            </ul>
        </>
    );
}

export default PpalMenu;

