import React from 'react';
import { useTranslation } from 'react-i18next';
import Divider from '@material-ui/core/Divider';

function LangMenu() {

    const { i18n } = useTranslation();

    return (
        <>
            <ul className="inline-list languages">
                <li className="equal-a" key='eus' onClick={() => i18n.changeLanguage('eus')}>eus</li>
                <Divider orientation="vertical" flexItem />
                <li className="equal-a" key='es' onClick={() => i18n.changeLanguage('es')}>es</li>
                <Divider orientation="vertical" flexItem />
                <li className="equal-a" key='cat' onClick={() => i18n.changeLanguage('cat')}>cat</li>       
            </ul>
        </>
    );
}

export default LangMenu;

    