import React, { useState, useEffect } from 'react';
import { useTranslation } from 'react-i18next';

import cartaEsp from '../../img/pr/parallevar.jpg';
import cartaEus from '../../img/pr/eramateko.jpg';

import Grid from '@material-ui/core/Grid';
import Box from '@material-ui/core/Box';

function TakeAway() {
    const { t, i18n } = useTranslation();
    const lang = i18n.language;
    const [load, setLoad] = useState(false);

    const printEspecialEusk = () => {
        if (lang === 'eus') {

            return (
                <>
                    <Box m={4} >
                        <p className="left-text">
                            <span className="text3 small">Laster</span>
                            <span className="text2">zure eskaria </span>
                            <span className="text1 bgContrast">ONLINE </span>
                            <span className="text2">egin ahal izango duzu!</span>
                        </p>
                        <p className="right-text">
                            <span className="text2">Oraingoz, <a href="tel:+34943271374"> 943271374</a></span>
                            <span className="text1">zenbakira deitu </span>
                            <span className="text2">edo</span>
                        </p>
                        <p>
                            <span className="text1">WHATSAPP bidez</span>
                            <span className="text2"><a href="tel:+34655845976">655845976 </a></span>
                            <span className="text1">zenbakira idatziaz </span>
                            <span className="text3 small">egin dezakezu.</span>
                        </p>
                    </Box>
                    <Box m={8}>
                        <picture>
                            <img src={cartaEus} alt='Imagen de la carta para llevar' />
                        </picture>
                    </Box>
                </>
            )
        } else {
            return (
                <>
                    <Box m={4} >
                        <p className="left-text">
                            <span className="text3 small">{t('pr.text1')}</span> <span className="text2">{t('pr.text2')}</span>
                            <span className="text1 bgContrast">{t('pr.text3')}</span>
                        </p>
                        <p>
                            <span className="text2">{t('pr.text4')}</span>
                            <span className="text1">{t('pr.text5')}</span>
                            <span className="text2">al <a href="tel:+34943271374"> 943271374</a></span>
                        </p>
                        <p className="right-text">
                            <span className="text2">{t('pr.text6')}</span>
                            <span className="text3 small">{t('pr.text7')}</span>
                            <span className="text2">al <a href="tel:+34655845976"> 655845976</a></span>
                        </p>
                    </Box>
                    <Box m={8}>
                        <picture>
                            <img src={cartaEsp} alt='Imagen de la carta para llevar' />
                        </picture>
                    </Box>
                </>
            )
        }
    }

    return (
            <>

            <div className="text-div">
                <Grid
                    container
                    direction="column"
                    justify="center"
                    alignItems="center"
                >
                    <Grid item>
                        {printEspecialEusk()}
                    </Grid>
                </Grid>
            </div>
        </>
    );
}


export default TakeAway;