import i18n from "i18next";
import { initReactI18next } from "react-i18next";
import LanguageDetector from 'i18next-browser-languagedetector';

import translationEUS from './translations/translationEUS.json';
import translationES from './translations/translationES.json';
import translationCAT from './translations/translationCAT.json';


const resources = {
    eus: {
        translation: translationEUS
    },
    es: {
        translation: translationES
    },
    cat: {
        translation: translationCAT
    }
};

i18n
    .use(initReactI18next)
    .use(LanguageDetector)
    .init({
        resources,
        lng: "es",
        fallbackLng: "es",
        interpolation: {
            escapeValue: false
        }
    });
