import React from 'react';
import { useTranslation } from 'react-i18next';
import { Link } from "react-router-dom";

import gallery01 from '../img/home/gallery01.png';
import gallery02 from '../img/home/gallery02.png';
import gallery03 from '../img/home/gallery03.png';
import gallery04 from '../img/home/gallery04.png';
import gallery05 from '../img/home/gallery05.png';
import gallery06 from '../img/home/gallery06.png';
import gallery07 from '../img/home/gallery07.png';
import gallery08 from '../img/home/gallery08.png';
import gallery09 from '../img/home/gallery09.png';
import gallery01w from '../img/home/gallery01.webp';
import gallery02w from '../img/home/gallery02.webp';
import gallery03w from '../img/home/gallery03.webp';
import gallery04w from '../img/home/gallery04.webp';
import gallery05w from '../img/home/gallery05.webp';
import gallery06w from '../img/home/gallery06.webp';
import gallery07w from '../img/home/gallery07.webp';
import gallery08w from '../img/home/gallery08.webp';
import gallery09w from '../img/home/gallery09.webp';

import Title from '../components/Title';

import Grid from '@material-ui/core/Grid';
import Box from '@material-ui/core/Box';

import DoubleArrow from '@material-ui/icons/DoubleArrow';
import NewReleasesRoundedIcon from '@material-ui/icons/NewReleasesRounded';


const galleryLine = (img1, img2, img3, img1w, img2w, img3w, smSize, classN) => {
    const altText = "Imagen de comida";

    return (

        <Grid item>
            <Grid
                container
                direction="row"
                justify="center"
                alignItems="center"
                wrap="wrap"
                spacing={1}
                className={classN}
                style={{ textAlign: "center" }}
            >
                <Grid item xs={12} sm={smSize}>
                    <picture>
                        <source srcSet={img1w} type="image/webp" />
                        <source srcSet={img1} type="image/png" />
                        <img src={img1} alt={altText} />
                    </picture>
                </Grid>
                <Grid item xs={12} sm={smSize}>
                    <picture>
                        <source srcSet={img2w} type="image/webp" />
                        <source srcSet={img2} type="image/png" />
                        <img src={img2} alt={altText} />
                    </picture>
                </Grid>
                <Grid item xs={12} sm={smSize}>
                    <picture>
                        <source srcSet={img3w} type="image/webp" />
                        <source srcSet={img3} type="image/png" />
                        <img src={img3} alt={altText} />
                    </picture>
                </Grid>

            </Grid>
        </Grid>
    )
}


const homeLinks = (linkText, text, linkpage) => {
    return (
      <div className="max-width-cont">
        <div className="home-link">
                <Link to={linkpage}><div className="cont-all-center"><p>{linkText}</p><DoubleArrow /></div></Link>  
        </div>
        <div className="home-link">
                <p className="home-link-text">{text}{' '}<a href="tel:+34943271374">943271374</a></p><NewReleasesRoundedIcon id="hide-icon"/>
        </div>
      </div>
    )
}

function Home() {
    const { t, i18n } = useTranslation();
    const lang = i18n.language;

    const printEspecialEusk = () => {
        if (lang === 'eus') {
            return (
                <h1><span>{t('home.titleA')}</span>{' '}<br/>{t('home.titleB')}</h1>
            )
        } else {
            return (
                <h1>{t('home.titleA')}<br /><span>{' '}{t('home.titleB')}</span></h1>
            )
        }
    }

    return (
        <>
            <div className="img-cont" id="rusa">
                <div className="text-content">
                    {printEspecialEusk()}
                </div>   
                <div className="author-content">
                   <p className="author">Imagen: <a href="https://www.instagram.com/gastrolovers/" target="_blank" rel="noopener noreferrer">Gastrolovers</a></p>
                </div> 
            </div>

            <div className="spacer"></div>

            <Box pb={2}>
                <Title title={t('home.title-to-menu')} />
                <Grid
                    container
                    direction="column"
                    justify="center"
                    alignItems="center" 
                    spacing={1}
                >
                    <Grid item className="home-text text-div">
                        <div>
                            <p>
                                <span className="text2 small">{t('home.text-menu1A')}</span>
                                <span className="text3 small">{t('home.text-menu1B')}</span>
                                <span className="text2 small">{t('home.text-menu1C')}</span>
                                <span className="text1 small no-mr"><Link to="/carta">{t('home.text-menu1D')}</Link></span>
                                <span className="text2 small no-ml">!</span>
                            </p>
                           
                        </div>
                    </Grid>

                    {galleryLine(gallery06, gallery04, gallery05, gallery06w, gallery04w, gallery05w, 3, '')}
                    {galleryLine(gallery01, gallery02, gallery03, gallery01w, gallery02w, gallery03w, 3, 'hide-grid')}

                    <Grid item className="home-text text-div">
                        <div>
                            <p>
                                <span className="text2 small">{t('home.text-menu2A')}</span>
                                <span className="text4 small bgContrast">{t('home.text-menu2B')}</span>
                                <span className="text2 small">{t('home.text-menu2C')}</span>
                                <span className="text1 small no-mr">{t('home.text-menu2D')}</span>.
                            </p>
                        </div>
                    </Grid>
                   
                </Grid>

                {homeLinks(t('home.link-to-menu2'), t('home.text-menu3'), "/carta")}  
                
            </Box>

            <div className="spacer"></div>

            <div className="full-img es" id="cafe">
                <div>
                    <p className="cafe0"> {t('home.cafe0')}</p>
                    <p className="cafe1"> <span>{t('home.cafe1')}</span></p>
                    <p className="cafe2">{t('home.cafe2')}</p>
                    <div>
                        <p className="cafe3">{t('home.cafe3')}</p>
                    </div>
                    <p className="cafe0">{t('home.cafe4')}</p>
                </div>
            </div>

            <div className="spacer"></div>

            <Box pb={4}>

                <Title title={t('home.title-to-takeaway')} />

                <Grid
                    container
                    direction="column"
                    justify="center"
                    alignItems="center"
                    spacing={1}
                >

                    <Grid item className="home-text text-div second-home-div">
                        <div>
                            <p id="specialText">
                                <span className="text2 small">{t('home.text-takeaway1A')}</span>
                                <br />
                                <span className="text1 small">{t('home.text-takeaway1B')}</span>
                                <br />
                                <span className="text4 small bgContrast">{t('home.text-takeaway1C')}</span>
                            </p>
                        </div>
                    </Grid>

                    {galleryLine(gallery07, gallery08, gallery09, gallery07w, gallery08w, gallery09w, 4, 'hide-grid')} 

                    <Box className="show-on-landscape">
                        <picture>
                            <source srcSet={gallery07w} type="image/webp" />
                            <source srcSet={gallery07} type="image/png" />
                            <img src={gallery07} alt='imagen de bolsa para llevar' />
                        </picture>
                    </Box>
                   
                </Grid>

                {homeLinks(t('home.link-to-takeaway'), t('home.text-takeaway1D'), '/para-llevar')}
            </Box>
           
            <div className="spacer"></div>
        </>
    );
}

export default Home;